import { useStaticQuery, graphql } from "gatsby"

const useSiteMetadata = () => {
  const { site } = useStaticQuery(
    graphql`
      query SITE_METADATA_QUERY {
        site {
          siteMetadata {
            title
            description
            author
            nugetVersion
          }
        }
      }
    `
  )

  return site.siteMetadata
}

export default useSiteMetadata
